          this.percent = num * 10
          if (num == 9) {
            this.imgEnd1 = true
          }
          if (num == 5) {
            this.imgEnd2 = true
          }
          if (num == 2) {
            this.imgEnd3 = true
          }
          if (num == 0) {
            clearInterval(this.t)
            this.t = null
          }
        }, num * 40)
      })
      // this.togglePlay()
