            break;
          case 5:
            this.imgContent = '第三不是目标，而是只有不断追求规模，宇视才有高质量的生存、可靠的供应链安全保障。宇视2014年起稳居中国前三，此后在全球市场陆续超越美国、德国、日本、韩国企业，2018起位居全球第四。'
            break;
          case 6:
            this.imgContent = '2016年，宇视发布「SMV机器视觉」战略，算法、架构、产品全面突破，陆续攻克2017年AI工程化、2018年AI场景化、2019年AI普惠化。宇视AIoT旗舰产品，全部采用中国山关湖泊命名，截至2021年共有“九山四关两湖'
            break;
          case 7:
            this.imgContent = '宇视以智慧城市大脑+人脸识别技术亮相6分钟，10万人海中25分钟找回走失老人。《大国重器Ⅱ》剧组评价：“我们一共拍摄63家企业，在交流和拍摄中被这家公司的员工面貌和产品气质深深打动，由衷欣赏宇视；我们不是生在一个平安的地球，而是生在一个安全的国家。'
            break;
          case 8:
            this.imgContent = '2018年8月，“英宇商标案”终审结果，宇视胜诉，驳回英特尔公司主张占用“inside”的诉讼请求。双方知识产权官司的焦点是，美国芯片“intel inside”和中国操作系统“imos inside”的商标纠纷。'
            break;
          case 9:
            this.imgContent = '智能交通+智慧物联，稳稳向前，更好的支撑AI实战落地，有自己比肩时代的节奏。'
            break;
          case 10:
            this.imgContent = '响应抗疫需求，宇视研发「热影」智能测温仪，28天定型量产，在桐乡基地创下7天15条新生产线的记录，价优量足供应世界需求，在全球80个国家和地区成为爆款，各国使领馆感谢信成为宇视办公室骄傲的陈设。'
            break;
          case 11:
            this.imgContent = '全社会与新冠病毒做长期斗争，宇视「防疫兵器库」的四件兵器是：测-热影66一验通行、隔-智能门磁、判-VDS大数据一机索迹，管-DataU数据可视化一目了然'
            break;
