<template>
  <div class="poster-in">
    <div ref="imgContainer" id="imgContainer" >
      <img src="../assets/image/logo.png" alt="" class="logo">
      <div class="bgImg">
        <div class="inner-content">
          <div class="inner-img">
            <img :src="imgObj.imgSrc" alt="">
          </div>
          <div class="inner-word">
            <div>
              <div>十年有你，初心不变</div>
              <div style="margin-left: 0.1rem">感谢所有的相遇、渡过与成长！</div>
              <div class="nameBox">{{ imgObj.name }}：</div>
              <div>与宇视相识{{ imgObj.days }}天了</div>
              <div>十年磨一剑，再战仍少年</div>
              <div>携手共谱新篇章</div>
            </div>
          </div>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
  import html2canvas from 'html2canvas';
  export default {
    name: "poster",
    data(){
      return{
        imgObj:{
          imgSrc:'',
          days:'',
          name:''
        }
      }
    },
    methods:{
      getResult(obj){
        this.imgObj.imgSrc = obj.imgSrc
        this.imgObj.days = obj.days
        this.imgObj.name = obj.name
        let dom = document.getElementById('imgContainer')
        this.$nextTick(() =>{
          html2canvas(this.$refs.imgContainer, {
            // 转换为图片,
            taintTest: false,
            allowTaint: false,
            scale:window.devicePixelRatio,
            width:dom.offsetWidth,//设置canvas尺寸与所截图尺寸相同，防止白边
            height:dom.offsetHeight,//防止白边
            useCORS: true // 解决资源跨域问题
          }).then(canvas => {
            let dataURL = canvas.toDataURL('image/jpeg',1);
            this.$emit('exportImg',dataURL)
          });
        })
      }
    }
  }
</script>

